	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"SMS Spam Wanted","date":"31 Dec 2003","slug":"/2003-12-31-sms-spam-wanted/","tags":"Y2003"},"html":"<p>Russell Beattie <a title=\"Russell Beattie Notebook\" href=\"http://www.russellbeattie.com/notebook/1005546.html\">wants you to send him an SMS</a> for the New Year. Maybe I should give his number to my cell provider <a href=\"http://www.eplus.de/\">e-plus</a>? They send enough SMS spam to keep anyone happy!</p>"},"next":{"fields":{"title":"Terror Alarm in Hamburg","date":"30 Dec 2003","slug":"/2003-12-30-terror-alarm-in-hamburg/"}},"previous":{"fields":{"title":"Update: Terror Alarm in Hamburg","date":"31 Dec 2003","slug":"/2003-12-31-update-terror-alarm-in-hamburg/"}}},{"node":{"fields":{"title":"Terror Alarm in Hamburg","date":"30 Dec 2003","slug":"/2003-12-30-terror-alarm-in-hamburg/","tags":"Y2003"},"html":"<p>The Bundeswehr Hospital in Hamburg was supposed to be target of a <a title=\"NDR Online: Terrorismus - Terroranschlag auf Krankenhaus geplant\" href=\"http://www.ndr.de/ndr/regional/detail_line.phtml?docid=20031230164945\">car bomb terrorist attack</a>, according to the Interioir Ministry in Hamburg. The area around the hospital in Hamburg Wandsbek was sealed off this afternoon by police. ARD is reporting that the warning came from the US, namely the CIA. <a title=\"BBC NEWS | World | Europe | Security alert at German hospital\" href=\"http://news.bbc.co.uk/1/hi/world/europe/3357553.stm\">BBC</a>, <a title=\"NETZEITUNG: Anschlag auf Hamburger Bundeswehr-Krankenhaus geplant\" href=\"http://www.netzeitung.de/spezial/kampfgegenterror/267089.html\">Netzeitung</a>, <a title=\"heute.t-online.de - Terror-Alarm in Hamburg\" href=\"http://www.heute.t-online.de/ZDFheute/artikel/3/0,1367,POL-0-2092899,00.html\">ZDF</a> and <a title=\"n-tv.de - Anschlag in Hamburg vereitelt\" href=\"http://www.n-tv.de/5201191.html\">n-tv</a> also have the story.</p>"},"next":{"fields":{"title":"Partytime","date":"28 Dec 2003","slug":"/2003-12-28-partytime/"}},"previous":{"fields":{"title":"SMS Spam Wanted","date":"31 Dec 2003","slug":"/2003-12-31-sms-spam-wanted/"}}},{"node":{"fields":{"title":"Partytime","date":"28 Dec 2003","slug":"/2003-12-28-partytime/","tags":"Y2003"},"html":"<p>Today we had all the relatives over for Christopher's birthday (a day late, but he can't read a calendar yet). As a highlight we served <a title=\"code: theWebSocket; - Cheesecake\" href=\"http://www.ahawkins.org/mt/archives/000157.html\">Alwin's cheesecake</a>, which went over very well with the guests, even my mother-in-law the super-baker (although next time I will use <em>Schmand</em> instead of <em>Saure Sahne</em> for sour cream). And Christopher scored more presents. But now he should be gifted out for the next year.</p>"},"next":{"fields":{"title":"Earthquake in Iran","date":"27 Dec 2003","slug":"/2003-12-27-earthquake-in-iran/"}},"previous":{"fields":{"title":"Terror Alarm in Hamburg","date":"30 Dec 2003","slug":"/2003-12-30-terror-alarm-in-hamburg/"}}}],"pathPrefix":"","first":false,"last":false,"index":562,"pageCount":1101}}

